.section-header {
  margin-bottom: 50px;
  margin-top: 50px;
}

.section-header .section-title {
  font-size: 36px;
  color: #454545;
  text-transform: uppercase;
  position: relative;
  padding-bottom: 20px;
  margin: 0 0 20px;
  font-family: 'SukhumvitSet-Medium';
  font-weight: 600;
}

.section-header .section-title:before {
  content: "";
  position: absolute;
  width: 140px;
  bottom: 0;
  left: 50%;
  margin-left: -70px;
  height: 2px;
  background: #00BFFF;
}

.section-header .section-title:after {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  bottom: -11px;
  left: 50%;
  margin-left: -12px;
  border: 5px solid #fff;
  border-radius: 20px;
  background: #00BFFF;
}

.btn {
